/* =========== Google Fonts ============ */
@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap");

/* =============== Globals ============== */
* {
  font-family: "Ubuntu", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --blue: #2a2185;
  --white: #fff;
  --gray: #f5f5f5;
  --black1: #222;
  --black2: #999;
}

body {
  min-height: 100vh;
  overflow-x: hidden;
}

.container {
  position: relative;
  width: 100%;
}

/* =============== Navigation ================ */
/* =============== Navigation ================ */
/* =============== Navigation ================ */
/* =============== Navigation ================ */
.navigation {
  position: fixed;
  width: 300px;
  height: 100%;
  background: var(--blue);
  border-left: 10px solid var(--blue);
  transition: 0.5s;
  overflow: hidden;
}
.navigation.active {
  width: 80px;
}

.navigation ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}



.navigation ul li {
  position: relative;
  width: 100%;
  list-style: none;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

.navigation ul li:hover,
.navigation ul li.hovered {
  background-color: var(--white);
}

.navigation ul li:nth-child(1) {
  margin-bottom: 40px;
  pointer-events: none;
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  display: flex;
  text-decoration: none;
  color: var(--white);
}
.navigation ul li:hover a,
.navigation ul li.hovered a {
  color: var(--blue);
}

.navigation ul li a .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 75px;
  text-align: center;
}
.navigation ul li a .icon ion-icon {
  font-size: 1.75rem;
}

.navigation ul li a .title {
  position: relative;
  display: block;
  padding: 0 10px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
}

/* Adjust the border-radius here for the dropdown */
.has-dropdown .dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px; /* Adjust width as needed */
  z-index: 1;
  background: #f9f9f9; /* Background color for the dropdown */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Optional: Add shadow for the dropdown */
  top: 100%;
  left: 0;
  border-radius: 0; /* Remove border-radius to remove the curve effect */
}

.has-dropdown:hover .dropdown-content {
  display: block;
}

.has-dropdown .dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.has-dropdown .dropdown-content a:hover {
  background-color: #f1f1f1;
}

.navigation ul li:hover a::before,
.navigation ul li.hovered a::before,
.navigation ul li:hover a::after,
.navigation ul li.hovered a::after {
  content: none;
}

.has-submenu .submenu {
  display: none;
  position: absolute;
  background: #802929; /* Background color for the submenu */
  box-shadow: 0 0 10px rgba(171, 22, 22, 0.1); /* Optional: Add shadow for the submenu */
  z-index: 1;
}

.has-submenu:hover .submenu {
  display: block;
}

