/* Updated styles for collapsed menu icon */
.navigation .toggle {
  cursor: pointer;
  padding: 10px;
  transition: transform 0.3s ease; /* Add transition for smooth animation */
}

.navigation .toggle span {
  height: 3px;
  width: 25px;
  background-color: #333;
  margin: 4px 0;
  display: block;
  transition: background-color 0.3s ease; /* Add transition for smooth animation */
}

.navigation .toggle.active {
  transform: rotate(45deg);
}

.navigation .toggle.active span:nth-child(1) {
  transform: rotate(-45deg);
  margin: 0;
}

.navigation .toggle.active span:nth-child(2) {
  opacity: 0;
}

.navigation .toggle.active span:nth-child(3) {
  transform: rotate(-45deg);
  margin: 0;
}
/* End of updated styles for collapsed menu icon */
